import axios from "axios";
import "../CSS/Payment.css";
import { BrowserRouter, Route, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
const Payment = ({
  name,
  phone,
  address,
  zoneCode,
  email,
  roomRole,
  sum,
  price,
  verCheckNum,
  car,
  adult,
  child,
  baby,
  startDate,
  endDate,
  loginId,
}) => {
  const nav = useNavigate();
  const [myId, setMyId] = useState("");
  useEffect(() => {
    setMyId(loginId);
    console.log(loginId);
  }, []);
  const changPage = () => {
    nav("/reservation/complete", {
      state: {
        pg: "html5_inicis", // PG
        pay_method: "card", // 
        merchant_uid: `mid_${new Date().getTime()}`, // 
        amount: 10, // 
        name: "   ", // 
        buyer_userId: myId,
        buyer_name: name, //  
        buyer_tel: phone, //  
        buyer_email: email, //  
        buyer_addr: address, //  
        buyer_postcode: zoneCode, //  
        buyer_car: car, //  
        buyer_reservation: {
          room_role: roomRole,
          room_adult: adult,
          room_child: child,
          room_baby: baby,
          room_price:
            roomRole === "standard"
              ? price[0]
              : roomRole === "deluxe"
              ? price[1]
              : roomRole === "luxury"
              ? price[2]
              : "",
          room_day: sum + "",
          room_resert_price:
            roomRole === "standard"
              ? price[0] * sum
              : roomRole === "deluxe"
              ? price[1] * sum
              : roomRole === "luxury"
              ? price[2] * sum
              : "",
          room_startDate: startDate,
          room_endDate: endDate,
        },
      },
    });
  };
  console.log(myId);
  function onClickPayment() {
    /* 1.   */
    const { IMP } = window;
    IMP.init("imp31382016");

    /* 2.    */
    const data = {
      pg: "html5_inicis", // PG
      pay_method: "card", // 
      merchant_uid: `mid_${new Date().getTime()}`, // 
      amount: 10, // 
      name: "   ", // 
      buyer_userId: myId,
      buyer_name: name, //  
      buyer_tel: phone, //  
      buyer_email: email, //  
      buyer_addr: address, //  
      buyer_postcode: zoneCode, //  
      buyer_car: car, //  
      buyer_reservation: {
        room_role: roomRole,
        room_adult: adult,
        room_child: child,
        room_baby: baby,
        room_price:
          roomRole === "standard"
            ? price[0]
            : roomRole === "deluxe"
            ? price[1]
            : roomRole === "luxury"
            ? price[2]
            : "",
        room_day: sum + "",
        room_resert_price:
          roomRole === "standard"
            ? price[0] * sum
            : roomRole === "deluxe"
            ? price[1] * sum
            : roomRole === "luxury"
            ? price[2] * sum
            : "",
        room_startDate: startDate,
        room_endDate: endDate,
      },
    };
    console.log(data);

    /* 4.    */
    IMP.request_pay(data, callback);

    /* 3.    */
    function callback(response) {
      const { success, error_msg } = response;

      if (success) {
        alert(" ");
        axios
          .post("/order", data)
          .then((res) => {
            console.log("", res.data);
            changPage();
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        alert(` : ${error_msg}`);
      }
    }
  }

  return (
    <>
      <div className="payment">
        <div className="payment-box">
          <div className="payment-main">
            <span className="payment-box-text">
              {roomRole === "standard"
                ? ""
                : roomRole === "deluxe"
                ? ""
                : roomRole === "luxury"
                ? ""
                : ""}
            </span>
          </div>
          <div className="payment-box-num">
            <span>
              {roomRole === "standard"
                ? price[0]
                : roomRole === "deluxe"
                ? price[1]
                : roomRole === "luxury"
                ? price[2]
                : ""}
              
            </span>
          </div>
        </div>
        <span className="payment-x">*</span>
        <div className="payment-box" style={{ marginLeft: "20px" }}>
          <div className="payment-main">
            <span className="payment-box-text" style={{ left: "25px" }}>
              
            </span>
          </div>
          <div className="payment-box-num">
            <span>{sum}</span>
          </div>
        </div>
        <span className="payment-y">=</span>
        <div className="payment-box">
          <div className="payment-main">
            <span className="payment-box-text"></span>
          </div>
          <div className="payment-box-num">
            <span>
              {roomRole === "standard"
                ? price[0] * sum
                : roomRole === "deluxe"
                ? price[1] * sum
                : roomRole === "luxury"
                ? price[2] * sum
                : ""}
              
            </span>
          </div>
        </div>
        <div className="payment-box">
          {verCheckNum && email !== "" ? (
            <div onClick={onClickPayment} className="payment-pay">
              <span className="payment-pay-text"></span>
            </div>
          ) : (
            <div className="payment-pay2">
              <span className="payment-pay-text"></span>
            </div>
          )}
        </div>
      </div>
    </>
  );
};
export default Payment;
