import { useLocation } from "react-router-dom";

const ReservationComplete = () => {
  const { state } = useLocation();
  console.log(state);
  return (
    <>
      <div>: {state.name}</div>
      <div>: {state.buyer_userId}</div>
      <div>: {state.buyer_name}</div>
      <div>: {state.buyer_tel}</div>
      <div>: {state.buyer_email}</div>
      <div>: {state.buyer_postcode}</div>
      <div>: {state.buyer_addr}</div>
      <div>: {state.buyer_car ? "Yes" : "No"}</div>
      <div>
        <h3>Reservation Details</h3>

        <div>: {state.buyer_reservation.room_role}</div>
        <div>
          : {state.buyer_reservation.room_startDate.getFullYear()}{" "}
          {state.buyer_reservation.room_startDate.getMonth() + 1}{" "}
          {state.buyer_reservation.room_startDate.getDate()}
        </div>
        <div>: {state.buyer_reservation.room_endDate.getFullYear()}{" "}
          {state.buyer_reservation.room_endDate.getMonth() + 1}{" "}
          {state.buyer_reservation.room_endDate.getDate()}</div>
        <div>: {state.buyer_reservation.room_adult}</div>
        <div>: {state.buyer_reservation.room_child}</div>
        <div>: {state.buyer_reservation.room_baby}</div>
        <div>: {state.buyer_reservation.room_price}</div>
        <div>: {state.buyer_reservation.room_day}</div>
        <div>: {state.buyer_reservation.room_resert_price}</div>
      </div>
    </>
  );
};
export default ReservationComplete;
